.radiomark {
  position: absolute;
  top: 0;
  left: 0;
  background-color: #FFFFFF;
  border-radius: 50%;
  border: 0.063rem solid #A0A0A0 !important;
  margin: 0 !important;
  width: 40px;
  width: 2.5rem;
  height: 40px;
  height: 2.5rem; }

